body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue" "CenturyGothic",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

@font-face {
    font-family: 'CenturyGothic';
    src: url('https://cdn.staticaly.com/gh/yourshansel/portfolio/6526c07b/src/fonts/CenturyGothic.ttf');
}

@font-face {
    font-family: 'CenturyGothic-Bold';
    src: url('https://cdn.staticaly.com/gh/yourshansel/portfolio/6526c07b/src/fonts/GOTHICB.TTF');
}

